import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { useIntl, FormattedMessage, Link } from 'gatsby-plugin-intl';

import { Query, AllorsSerialisedItem } from '../../graphql-types';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Separator } from '../components/separator';
import Item from '../components/item';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';

function shuffle(src: AllorsSerialisedItem[]): AllorsSerialisedItem[] {
  const dst = src?.slice(0) ?? [];
  for (let i = dst.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [dst[i], dst[j]] = [dst[j], dst[i]];
  }
  return dst;
}

const IndexPage: React.FunctionComponent<{ data: Query }> = ({ data }) => {
  const intl = useIntl();
  const { allFile, allAllorsSerialisedItem, allorsCatalogue } = data;

  const validOffers = allAllorsSerialisedItem.nodes?.filter((v) => v.primaryPhoto?.file.childImageSharp);
  const offers = shuffle(validOffers).slice(0, 4);

  const [isPlaying, setIsPlaying] = React.useState(false);

  const heroshot = allFile.edges.find((v) => v.node.name === 'heroshot').node.childImageSharp.fluid;
  const quote = allFile.edges.find((v) => v.node.name === 'quote').node.childImageSharp.fluid;

  const productCategories = [...allorsCatalogue.productCategories].sort((a, b) => a.name && b.name && a.name.localeCompare(b.name));

  function renderAbout(locale: string) {
    switch (locale) {
      case 'fr':
        return (
          <>
            <h1>À propos d&apos;Aviaco</h1>
            <p>
              Aviaco est une multinationale, spécialisée dans la fourniture et la gestion d&apos;équipements de GSE pour le secteur de
              l&apos;aviation.
            </p>
            <h2>Nos services de base</h2>
            <ul className="fa-ul">
              <li>
                <FontAwesomeIcon icon="check" /> Vente et achat de matériel d&apos;assistance au sol de haute qualité dans le monde entier
              </li>
              <li>
                <FontAwesomeIcon icon="check" /> Location GSE court et long terme
              </li>
              <li>
                <FontAwesomeIcon icon="check" /> Réparation &amp; maintenance
              </li>
              <li>
                <FontAwesomeIcon icon="check" /> Gestion de flotte
              </li>
            </ul>
          </>
        );
      case 'es':
        return (
          <>
            <h1>Sobre Aviaco</h1>
            <p>
              Aviaco es una empresa multinacional centrada en el suministro y la gestión de equipos de apoyo en tierra (GSE) para el sector
              de la aviación.
            </p>
            <h2>Nuestros servicios centrales</h2>
            <ul className="fa-ul">
              <li>
                <FontAwesomeIcon icon="check" /> Compra y venta de equipos de apoyo en tierra de alta calidad en todo el mundo
              </li>
              <li>
                <FontAwesomeIcon icon="check" /> Alquiler de equipos a corto y largo plazo
              </li>
              <li>
                <FontAwesomeIcon icon="check" /> Reparación y Mantenimiento
              </li>
              <li>
                <FontAwesomeIcon icon="check" /> Gestión de flotas
              </li>
            </ul>
          </>
        );
      default:
        return (
          <>
            <h1>About Aviaco</h1>
            <p>
              Aviaco is a multinational company focused on the supplying and management of Ground Support Equipment (GSE) for the Aviation
              sector.
            </p>
            <h2>Our Core Services</h2>
            <ul className="fa-ul">
              <li>
                <FontAwesomeIcon icon="check" /> Sales &amp; purchase of high-quality ground support equipment worldwide
              </li>
              <li>
                <FontAwesomeIcon icon="check" /> Short &amp; long term GSE rental
              </li>
              <li>
                <FontAwesomeIcon icon="check" /> Repair &amp; maintenance
              </li>
              <li>
                <FontAwesomeIcon icon="check" /> Fleet management
              </li>
            </ul>
          </>
        );
    }
  }

  return (
    <Layout>
      <SEO title="" description={intl.formatMessage({ id: 'home-description' })} />

      <main role="main">
        <section id="home-heroshot">
          <Container>
            <Row className="align-items-center">
              <Col lg="8">
                {heroshot && (
                  <>
                    {isPlaying && (
                      <div className="embed-responsive embed-responsive-16by9">
                        <iframe
                          title="Company Presentation Video"
                          className="embed-responsive-item"
                          src="https://www.youtube.com/embed/N-n8Ouxikhs?autoplay=1"
                          allow="autoplay; encrypted-media"
                        ></iframe>
                      </div>
                    )}

                    {!isPlaying && (
                      <div className="video-wrapper" onClick={() => setIsPlaying(true)}>
                        <Img
                          fluid={heroshot}
                          className="img-fluid heroshot"
                          alt="Aviaco,supplying and management of Ground Support Equipment"
                        />
                        <div className="play-btn"></div>
                      </div>
                    )}
                  </>
                )}
              </Col>

              <div className="col-lg-4 ">
                {renderAbout(intl.locale)}
                <Link to={'/about'} className="btn btn-primary">
                  <FormattedMessage id="read-more" /> <FontAwesomeIcon icon="angle-right" />
                </Link>
              </div>
            </Row>
          </Container>
        </section>

        <section>
          <Container>
            <Row>
              <Col md="8" lg="8">
                <h2>
                  <FormattedMessage id="current-offers" />
                </h2>
              </Col>
              <Col md="4" lg="4" className="text-right product-nav">
                <Link to="/offers" className="block">
                  <FormattedMessage id="current-offers" />
                </Link>
              </Col>
            </Row>
            <Separator />
            <Row>
              {offers.map((offer) => {
                return <Item key={offer.allorsId} item={offer}></Item>;
              })}
            </Row>
          </Container>
        </section>

        <section id="home-quote">
          <Container>
            <Row className="align-items-center no-gutters quote-bg">
              <Col lg="6" md="6" className="quote">
                <blockquote className="blockquote">
                  <p className="mb-0">
                    <FormattedMessage id="quote" />
                  </p>
                  <footer className="blockquote-footer">
                    <cite title="Source Title">A satisfied customer</cite>
                  </footer>
                </blockquote>
              </Col>
              <Col lg="6" md="6" className="d-none d-lg-block pl-0 pr-0">
                <Img fluid={quote} className="img-fluid" alt="A satisfied customer" />
              </Col>
            </Row>
          </Container>
        </section>

        <section>
          <Container>
            <Row>
              <Col md="8" lg="8">
                <h2>
                  <FormattedMessage id="categories" />
                </h2>
              </Col>
              <Col md="4" lg="4" className="text-right product-nav">
                <Link to="/catalogue" className="block">
                  <FormattedMessage id="catalogue" />
                </Link>
                <Link to="/brands" className="block">
                  <FormattedMessage id="brands" />
                </Link>
              </Col>
            </Row>
            <Separator />
            <Row>
              <Col md="12" lg="12" xl="12" className="order-lg-2">
                <Row>
                  {productCategories &&
                    productCategories.map((category) => {
                      const fluid = category.categoryImage.file.childImageSharp.fluid;
                      return (
                        <Col key={category.slug} lg="3" md="4" xs="6">
                          <Link to={`/${category.slug}`} key={category.slug} className="product-link">
                            <Card>
                              <Img fluid={fluid} />
                              <Card.Body>
                                <Card.Title>{category.name}</Card.Title>
                              </Card.Body>
                            </Card>
                          </Link>
                        </Col>
                      );
                    })}
                </Row>
              </Col>
            </Row>
          </Container>
        </section>
      </main>
    </Layout>
  );
};

export const query = graphql`
  {
    allorsCatalogue {
      name
      productCategories {
        slug
        name
        categoryImage {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
    allAllorsSerialisedItem(filter: { showOnFrontPage: { eq: true } }) {
      nodes {
        allorsId
        name
        itemNumber
        manufacturingYear
        primaryPhoto {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
        partWhereSerialisedItem {
          UnifiedGood {
            name
            slug
          }
        }
      }
    }
    allFile(filter: { name: { in: ["heroshot", "quote"] } }) {
      edges {
        node {
          name
          childImageSharp {
            id
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`;

export default IndexPage;
