import React from 'react';
import { Link } from 'gatsby-plugin-intl';

import Img from 'gatsby-image';
import { AllorsSerialisedItem } from '../../graphql-types';
import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';

const Item: React.FunctionComponent<{ item: AllorsSerialisedItem }> = ({ item }) => {
  const fluid = item.primaryPhoto ? item.primaryPhoto.file.childImageSharp.fluid : undefined;
  const good = item.partWhereSerialisedItem?.UnifiedGood;

  return (
    good && (
      <Col sm="12" md="3" lg="3">
        <Link to={`/${good.slug}`} className="product-link">
          <Card>
            {fluid && <Img fluid={fluid} />}
            <Card.Body>
              <Card.Title className="text-center">{good.name}</Card.Title>
            </Card.Body>
          </Card>
        </Link>
      </Col>
    )
  );
};

export default Item;
